import {authAPI} from "../api/api";
import {stopSubmit} from "redux-form";

const SET__USERS__DATA = 'SET__USERS__DATA-socialNetwork';


let initialState = {
    id: null,
    email: null,
    login: null,
    isAuth: false
};

const profileReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET__USERS__DATA: {
            return {
                ...state,
                ...action.data,
            }
        }

        default: {
            return state;
        }
    }
}


export const setUsersData = (id, email, login, isAuth) => ({type: SET__USERS__DATA, data: {id, email, login, isAuth}});


export const authData = () => {
    return async (dispatch) => {
        let data = await authAPI.getAuthData()
        const {id, login, email} = data.data;
        if (data.resultCode === 0) {
            dispatch(setUsersData(id, email, login, true));
        }
    }
}

export const login = (email, password, rememberMe) => {
    return async (dispatch) => {
        let data = await authAPI.login(email, password, rememberMe)
        if (data.resultCode === 0) {
            dispatch(authData());
        } else {
            const message = data.messages.length > 0 ? data.messages[0] : 'some Error';
            dispatch(stopSubmit('login', {_error: message}));
        }
    }
}

export const logout = () => {
    return async (dispatch) => {
        let data = await authAPI.logout()
        if (data.resultCode === 0) {
            dispatch(setUsersData(null, null, null, false));
        }
    }
}

export default profileReducer;