import React, {useEffect} from 'react';
import style from './Profile.module.css';
import profileBg from '../../assets/img/header_images.jpg';
import {useDispatch, useSelector} from "react-redux";
import {getUserProfile, getUserStatus, savePhoto} from "../../redux/profileReducer";
import Preloader from "../../common/Preloader/Preloader";
import {withRouter} from "react-router-dom";
import userImg from '../../assets/img/userAvatar.png';
import {Redirect} from 'react-router-dom';
import cn from 'classnames';
import {compose} from 'redux';
import ProfileStatus from "./ProfileStatus";

const ProfileInfo = (props) => {
    const {profile, status} = useSelector(state => state.profile);
    const {isAuth} = useSelector(state => state.auth);
    const authId = useSelector(state => state.auth.id);
    let userId = props.match.params.userId;
    if (!userId) {
        userId = authId;
        if (!userId) {
            props.history.push('/login');
        }
    }

    const dispatch = useDispatch();

    const getPhoto = (e) => {
        dispatch(savePhoto(e.target.files[0]));
    }

    useEffect(() => {
        dispatch(getUserProfile(userId));
        dispatch(getUserStatus(userId));
    }, [status, userId]);

    if (!isAuth) return <Redirect to={'/login'}/>

    {
        if (!profile) {
            return <Preloader/>
        } else {

            return (
                <>
                    <div className={style.background}>
                        <img className={style.image} src={profileBg} alt="Phone"/>
                    </div>

                    <div className={style.description}>
                        <div className={cn(style.avatar, style.infoAvatar)}>
                            <img className={style.avatarImg}
                                 src={!profile.photos.large ? userImg : profile.photos.large} alt="Avatar"/>
                            {!props.match.params.userId &&
                            <label htmlFor="file" className= {cn(style.fileButton,'button' )}>select photo<input className={style.selectFile} id='file' type="file" onChange={getPhoto}/></label>}
                        </div>
                        <ProfileStatus status={status}/>
                        <div className={style.descriptionBlock}>{profile.aboutMe}</div>
                        <div className={style.descriptionBlock}>{profile.contacts.facebook}</div>
                        <div className={style.descriptionBlock}>{profile.contacts.website}</div>
                        <div className={style.descriptionBlock}>{profile.contacts.vk}</div>
                        <div className={style.descriptionBlock}>{profile.contacts.twitter}</div>
                        <div className={style.descriptionBlock}>{profile.contacts.instagram}</div>
                        <div className={style.descriptionBlock}>{profile.contacts.youtube}</div>
                        <div className={style.descriptionBlock}>{profile.contacts.github}</div>
                        <div className={style.descriptionBlock}>{profile.contacts.mainLink}</div>
                        <div className={style.descriptionBlock}>{profile.lookingForAJob}</div>
                        <div className={style.descriptionBlock}>{profile.lookingForAJobDescription}</div>
                        <div className={style.descriptionBlock}>{profile.fullName}</div>
                    </div>
                </>
            )
        }
    }
}

export default compose(
    withRouter
)(ProfileInfo);
